import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DeleteAccountComponent } from './delete-account-page.component';


const routes: Routes = [{ path: '', component: DeleteAccountComponent },
{path:':id',component: DeleteAccountComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DeleteAccountRoutingModule { }



